body {
  background-image: url('beach-2836300.jpg');
  margin-top: -30px;
  margin-left: 5px;
  padding: 0px;
  background-repeat: no-repeat;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

.board {
  display: flex;
  flex-direction: row;
}

.list {
  min-width: 250px;
  max-width: 20%;;
  padding: 6px;
}

.list-title {
  padding-right: 10px;
  padding-left: 3px;
  background-color: #175382;
  color: #ffff;
}

.top-button {
  background-color: #175382;
  border: none;
  color: white;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  position: relative;
  top: 47px;
}

.top-button:hover {
  background-color:  #12456c;
  cursor: pointer;
}

.card-container {
  margin-top: -52px;
  margin-bottom: -50px;
}

.card {
  padding: 6px;
  padding-right: 25px;
  background-color: #f0f0f0;
  font-weight: 400;
  border-left: 6px solid #60eaae;
}

.add-card-container {
  color: #888;
  margin-top: -52px;
  margin-bottom: -50px;
}

.add-card {
  padding: 6px;
  padding-right: 25px;
  background-color: #eeeaea;
  font-weight: 500;
}

.add-card:hover,
.add-card:focus {
  background-color: #ded8d8;
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

label {
  font-size: 20px;
}

input {
  margin-top: 3px;
  margin-bottom: 1px;
  font-size: 20px;
}

input[type=submit] {
  background-color: #175382;
  border: none;
  color: white;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color:  #12456c;
}